// --- COMPONENTES UI ---

const SummaryCard = ({ title, value, subtitle, type = 'neutral' }: { title: string, value: string, subtitle: string, type?: 'neutral' | 'positive' | 'negative' }) => {
  const getTextColor = () => {
    if (type === 'positive') return 'text-green-600';
    if (type === 'negative') return 'text-red-600';
    return 'text-gray-800';
  };

  return (
    <div className="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm hover:shadow-md transition-shadow">
      <h3 className="text-xs font-bold text-gray-400 uppercase tracking-wider mb-2">{title}</h3>
      <p className={`text-2xl font-bold ${getTextColor()}`}>{value}</p>
      <p className="text-xs text-gray-400 mt-2">{subtitle}</p>
    </div>
  );
};

const ProgressBar = ({ percentage, colorClass = 'bg-gradient-to-r from-indigo-500 to-purple-600' }: { percentage: number, colorClass?: string }) => (
  <div className="h-4 w-full bg-gray-100 rounded-full overflow-hidden border border-gray-200">
    <div 
      className={`h-full ${colorClass} flex items-center justify-center text-[10px] font-bold text-white transition-all duration-500`}
      style={{ width: `${Math.min(percentage, 100)}%` }}
    >
      {percentage > 15 && `${Math.round(percentage)}%`}
    </div>
  </div>
);

const TransactionItem = ({ transaction }: { transaction: Transaction }) => (
  <div className="flex items-center justify-between p-4 bg-gray-50 border border-gray-100 rounded-xl hover:bg-white hover:border-indigo-100 transition-all group">
    <div className="flex items-center gap-4">
      <div className={`p-2 rounded-lg ${transaction.type === 'income' ? 'bg-green-100 text-green-600' : 'bg-red-100 text-red-600'}`}>
        {transaction.type === 'income' ? <TrendingUp size={20} /> : <TrendingDown size={20} />}
      </div>
      <div>
        <h4 className="font-semibold text-gray-800 group-hover:text-indigo-600 transition-colors">{transaction.title}</h4>
        <p className="text-xs text-gray-500">
          {new Date(transaction.date).toLocaleDateString('pt-BR')} ‚Ä¢ {transaction.category} ‚Ä¢ {transaction.recurring ? 'Recorrente' : 'Pontual'}
        </p>
      </div>
    </div>
    <span className={`font-bold text-lg ${transaction.type === 'income' ? 'text-green-600' : 'text-red-600'}`}>
      {transaction.type === 'expense' ? '- ' : '+ '}
      {transaction.value.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
    </span>
  </div>
);

