// --- COMPONENTE PRINCIPAL ---

export default function PoupApp() {
  const [activeTab, setActiveTab] = useState('dashboard');
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [transactions, setTransactions] = useState<Transaction[]>(MOCK_TRANSACTIONS);
  const [goals] = useState<Goal[]>(MOCK_GOALS);

  const handleAddTransaction = (type: 'income' | 'expense') => {
    const title = prompt(`Nova ${type === 'income' ? 'Receita' : 'Despesa'}:`);
    if (!title) return;
    const value = parseFloat(prompt("Valor (ex: 100.50):") || "0");
    if (!value) return;

    const newTransaction: Transaction = {
      id: Date.now(),
      title,
      value,
      type,
      date: new Date().toISOString(),
      category: 'Geral',
      recurring: false
    };
    setTransactions([newTransaction, ...transactions]);
  };

  const renderContent = () => {
    switch (activeTab) {
      case 'dashboard': return <Dashboard transactions={transactions} />;
      case 'receitas': return <TransactionPage title="Receitas" type="income" transactions={transactions} onAdd={() => handleAddTransaction('income')} />;
      case 'despesas': return <TransactionPage title="Despesas" type="expense" transactions={transactions} onAdd={() => handleAddTransaction('expense')} />;
      case 'orcamento': return <BudgetPage />;
      case 'metas': return <GoalsPage goals={goals} />;
      case 'relatorios': return <div className="p-10 text-center text-gray-500">Funcionalidade de Relat√≥rios em constru√ß√£o...</div>;
      case 'configuracoes': return <div className="p-10 text-center text-gray-500">Configura√ß√µes do Usu√°rio...</div>;
      default: return <Dashboard transactions={transactions} />;
    }
  };

  const NavItem = ({ id, icon: Icon, label }: { id: string, icon: any, label: string }) => (
    <button 
      onClick={() => { setActiveTab(id); setIsMobileMenuOpen(false); }}
      className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${
        activeTab === id 
          ? 'bg-gradient-to-r from-indigo-600 to-purple-600 text-white shadow-lg shadow-indigo-200' 
          : 'text-gray-600 hover:bg-indigo-50 hover:text-indigo-600'
      }`}
    >
      <Icon size={20} />
      {label}
      {activeTab === id && <ChevronRight size={16} className="ml-auto opacity-50" />}
    </button>
  );

  return (
    <div className="flex h-screen bg-gray-100 font-sans overflow-hidden text-gray-800">
      {/* Sidebar Desktop */}
      <aside className="hidden md:flex w-64 bg-white flex-col border-r border-gray-200 z-20">
        {/* √ÅREA DO MASCOTE NA SIDEBAR */}
        <div className="p-6 pb-8 border-b border-gray-100 flex flex-col items-center text-center">
          {/* Imagem do Mascote - AUMENTADO AO M√ÅXIMO (w-56 h-56) */}
          <img 
            src="/poup.png" 
            alt="POUP Mascote" 
            className="w-56 h-56 mb-4 object-contain drop-shadow-lg hover:scale-110 transition-transform duration-300 cursor-pointer"
          />
          <h1 className="text-3xl font-black bg-gradient-to-br from-indigo-600 to-purple-600 bg-clip-text text-transparent">
            POUP
          </h1>
          <p className="text-xs text-gray-400 mt-1">Finan√ßas Simplificadas</p>
        </div>
        
        <nav className="flex-1 p-4 space-y-2 overflow-y-auto">
          <NavItem id="dashboard" icon={LayoutDashboard} label="Dashboard" />
          <NavItem id="receitas" icon={TrendingUp} label="Receitas" />
          <NavItem id="despesas" icon={TrendingDown} label="Despesas" />
          <NavItem id="orcamento" icon={Wallet} label="Or√ßamento" />
          <NavItem id="metas" icon={Target} label="Metas" />
          <NavItem id="relatorios" icon={PieChart} label="Relat√≥rios" />
        </nav>

        <div className="p-4 border-t border-gray-100">
          <NavItem id="configuracoes" icon={Settings} label="Configura√ß√µes" />
          <button className="w-full flex items-center gap-3 px-4 py-3 mt-2 text-sm font-medium text-red-500 hover:bg-red-50 rounded-xl transition-colors">
            <LogOut size={20} /> Sair
          </button>
        </div>
      </aside>

      {/* Mobile Menu Button */}
      <div className="md:hidden fixed top-0 left-0 w-full bg-white p-4 border-b z-30 flex justify-between items-center shadow-sm">
        <div className="flex items-center gap-2">
           {/* Mascote Vers√£o Mobile (cabe√ßalho) - Aumentado para w-14 h-14 */}
           <img src="/poup.png" alt="Logo" className="w-14 h-14 object-contain" />
           <h1 className="font-black text-indigo-600 text-xl">POUP</h1>
        </div>
        <button onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}><Menu /></button>
      </div>

      {/* Mobile Sidebar */}
      {isMobileMenuOpen && (
        <div className="fixed inset-0 bg-black/50 z-40 md:hidden" onClick={() => setIsMobileMenuOpen(false)}>
          <div className="absolute left-0 top-0 h-full w-64 bg-white p-4 shadow-2xl" onClick={e => e.stopPropagation()}>
            <div className="flex justify-between items-center mb-8 border-b pb-4">
               <div className="flex items-center gap-2">
                 {/* Mascote Vers√£o Mobile (dentro do menu) - Aumentado para w-20 h-20 */}
                 <img src="/poup.png" alt="Logo" className="w-20 h-20 object-contain" />
                 <span className="font-bold text-lg text-gray-800">POUP</span>
               </div>
               <button onClick={() => setIsMobileMenuOpen(false)} className="text-gray-500"><X /></button>
            </div>
            <nav className="space-y-2">
              <NavItem id="dashboard" icon={LayoutDashboard} label="Dashboard" />
              <NavItem id="receitas" icon={TrendingUp} label="Receitas" />
              <NavItem id="despesas" icon={TrendingDown} label="Despesas" />
              <NavItem id="orcamento" icon={Wallet} label="Or√ßamento" />
              <NavItem id="metas" icon={Target} label="Metas" />
              <NavItem id="configuracoes" icon={Settings} label="Configura√ß√µes" />
            </nav>
          </div>
        </div>
      )}

      {/* Main Content */}
      <main className="flex-1 flex flex-col h-full overflow-hidden relative">
         {/* Header Desktop (User Info) */}
         <div className="hidden md:flex justify-end items-center p-6 px-8 gap-4 bg-gray-50/50 absolute top-0 right-0 w-full z-10 pointer-events-none">
          <div className="pointer-events-auto flex items-center gap-4">
             <button className="relative p-2 text-gray-400 hover:text-indigo-600 transition-colors">
               <Bell size={20} />
               <span className="absolute top-2 right-2 w-2 h-2 bg-red-500 rounded-full border border-white"></span>
             </button>
             <div className="flex items-center gap-3 pl-4 border-l border-gray-200">
               <div className="text-right">
                 <p className="text-sm font-bold text-gray-700">Usu√°rio Demo</p>
                 <p className="text-xs text-gray-400">Premium</p>
               </div>
               <div className="w-10 h-10 bg-gradient-to-br from-indigo-500 to-purple-600 rounded-full flex items-center justify-center text-white font-bold shadow-md">
                 UD
               </div>
             </div>
          </div>
        </div>

        <div className="flex-1 overflow-y-auto p-4 md:p-8 pt-20 md:pt-24 scroll-smooth">
          <div className="max-w-6xl mx-auto">
            {renderContent()}
          </div>
        </div>
      </main>
    </div>
  );
}